/*
  Timeline
*/

.timeline {
    position:relative;
    padding-top:40px;
    padding-bottom:40px;
  }
  
  .timeline:before {
      content:'';
      position:absolute;
      top:0;
      left:28px;
      height:100%;
      width:4px;
      background:#e8e8e8;
    }
  
  @media only screen and (min-width: 768px) {
  
  .timeline:before {
        left:50%;
        margin-left:-2px
    }
      }
  
  .timeline__block {
      position:relative;
      display:-ms-flexbox;
      display:flex;
      -ms-flex-direction:row;
          flex-direction:row;
      -ms-flex-wrap:wrap;
          flex-wrap:wrap;
      margin-bottom:40px;
      
    }
  
  @media only screen and (min-width: 768px) {
  
  .timeline__block {
        -ms-flex-wrap:nowrap;
            flex-wrap:nowrap;
        -ms-flex-align:start;
            align-items:flex-start;
        -ms-flex-pack:justify;
            justify-content:space-between
      
    }
      }
  
  .timeline__block:last-child {
        margin-bottom:0;
      }
  
  @media only screen and (min-width: 768px) {
  
  .timeline__block:nth-child(odd) .timeline__content {
            -ms-flex-order:-1;
                order:-1
        }
  .timeline__block:nth-child(odd) .timeline__headline {
    text-align: right;
  }
}
  
  .timeline__block:nth-child(odd) .timeline__info {
        }
  
  .timeline__block:nth-child(even) .timeline__content {
  
        }
  
  @media only screen and (min-width: 768px) {
  
  .timeline__block:nth-child(even) .timeline__info {
            -ms-flex-order:-1;
                order:-1;
            -ms-flex-pack:end;
                justify-content:flex-end
        }
          }
  
  .timeline__image {
      position:absolute;
      top:0;
      left:0;
  
      height:60px;
      width:60px;
      background-color:#d6d6d6;
      border-radius:50%;
      border:1px solid #e8e8e8;
    }
  
  @media only screen and (min-width: 768px) {
  
  .timeline__image {
        left:50%;
        margin-left:-30px
    }
      }
  
  .timeline__content,
    .timeline__info {
      width:calc(100% - 80px);
      margin-left:80px;
    }
  
  @media only screen and (min-width: 768px) {
  
  .timeline__content,
    .timeline__info {
        width:46%;
        width:calc(50% - 70px);
        margin-left:0
    }
      }
  
  .timeline__content {
      margin-bottom:10px;
      padding:20px;
      background-color:#fff;
      border:1px solid #e8e8e8;
    }
  
  @media only screen and (min-width: 768px) {
  
  .timeline__content {
        margin-bottom:0
    }
      }
  
  .timeline__content p {
        margin-top:10px;
        margin-bottom:10px;
      }
  
  .timeline__content p:last-child {
          margin-bottom:0;
        }
  
  .timeline__info {
      display:-ms-flexbox;
      display:flex;
    }
  
  @media only screen and (min-width: 768px) {
  
  .timeline__info {
        -ms-flex-align:center;
            align-items:center;
        min-height:60px
    }
      }
  
  .timeline__headline {
      margin-top:0;
      margin-bottom:10px;
      text-transform:uppercase;
      font-weight:700;
    }
  
  .timeline__date {
      font-size:1.2px;
      opacity:0.5;
    }
  
  
  /*
    Visuals - Not necessary for the timeline.
  */
  
  html{
  
      -webkit-box-sizing: border-box;
  
              box-sizing: border-box;
  }
  *,
    *:before,
    *:after{
  
      -webkit-box-sizing: inherit;
  
              box-sizing: inherit;
  }
  
  body {
    margin:0;
    font-family: sans-serif;
    font-size: 16px;
    line-height: 1.5;
    font-weight: 300;
    background:#f7f6f6;
    color:#333;
  }
  
  header {
    padding:40px;
    background:#fff;
    border-bottom:1px solid #e8e8e8;
  }
  
  h1 {
    margin-top:0;
  }
  
  h2 {
    margin-bottom:0;
  }
  
  main {
    padding-left:40px;
    padding-right:40px;
  }


